
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import {mapMutations} from 'vuex';
export default {
name:'login',
components:{
Group,XInput,XButton,Toast,Msg
},
data:function(){
return {
accesstoken:'',
isShowText:false,
tostText:'',
isShowMsg : false,
buttons:[{
type:'primary',
text:'',
link:'/topiclist/all'
}]
}
},
created:function(){
if(this.$store.state.islogined==true){
this.isShowMsg = true;
return ;
}
},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationId']),
dologin(){

var app = this
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(response){
if(response&&response.data&&response.data.success){





app.mutationLogin();
app.mutationLoginname(response.data.loginname);
app.mutationAccessToken(response.data.accessToken);
app.mutationId(response.data.id)
app.$router.push('/user/'+response.data.loginname);

window.localStorage.setItem('loginStatus',true);
window.localStorage.setItem('loginname',response.data.loginname);
window.localStorage.setItem('accessToken',app.accesstoken)
window.localStorage.setItem('author_id',response.data.id)
}
}).catch(function(error){

console.log(error.response)


app.isShowText = true;
app.tostText = error.response.data.error_msg
})
}

}
}



