import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//
Vue.use(Vuex);


//Store
const store = new Vuex.Store({
	strict:true,
	//
	state:{
		//
		topics:[
  			{name:'all',desc:'',selected:true},
  			{name:'good',desc:'',selected:false},
  			{name:'share',desc:'',selected:false},
  			{name:'ask',desc:'',selected:false},
  			{name:'job',desc:'',selected:false},
  			{name:'dev',desc:'',selected:false}
  		],
  		//
  		islogined:false,
  		//
  		isShowMoreMenus:false,
  		//
		unloginmenus:{
			login:"",
			menu2:""
		},
		//
		loginname:'',
		//accesstoken
		accesstoken:'',
		//
		loginmenus:{
			create:'',
			center:'',
			msg:'',
			logout:''
		},
		//Id
		author_id:''
	},
	//
	getters:{
		//
		moreMenus(state){
			if(state.islogined){
				return state.loginmenus;
			}
			return state.unloginmenus
		},
		getTopicDescByTab:function(state){
			return function(tab){
				console.log(tab)
				for(var index = 0;index<state.topics.length;index++){
					if(state.topics[index].name == tab){
						return state.topics[index].desc
					}
				}
			}
		}
	},
	mutations,
	actions
});

//
export default store;



