const mutations = {
	mutationIsShowMoreMenus:function(state,payload){
		//state
		//payload
		state.isShowMoreMenus = payload
	},
	mutationLogin:function(state){
		state.islogined = true;
	},
	mutationLoginname:function(state,name){
		state.loginname = name
	},
	mutationAccessToken:function(state,access){
		state.accesstoken = access
	},
	mutationLogout:function(state){
		state.islogined = false;
		state.loginname = '';
		state.accesstoken = '';
	},
	mutationId:function(state,id){
		state.author_id = id
	}
}

export default mutations;



