
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name:'edit',
props:['id'],
components:{
Group,XTextarea,
XButton,
mavonEditor
},
data:function(){
return {
topic:{
title:'',
content:''
}
}
},
methods:{

edittopic(){
var app = this;
this.$http.post('/topics/update',{
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: 'dev'
}).then(function(response){

app.$router.back()
})
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(response){
vm.topic = response.data.data;
vm.topic.content = vm.topic.content.slice(30,-11)
})
})
},
beforeRouteUpdate:function(to,from,next){
var app = this;
this.$http.get('/topic/'+to.params.id).then(function(response){
app.topic = response.data.data;
app.topic.content = app.topic.content.slice(30,-11)
})
}
}



