
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios'
export default{
name:'create',
data:function(){
return {
title:'',
content:'',

toolbars: {
bold: true, // 
italic: true,// 
header: true,// 
underline: true,// 

mark: true,// 


quote: true,// 
ol: true,// 
ul: true,// 
link: true,// 
imagelink: true,// 
code: true,// code
table: true,// 
subfield: true,// 
fullscreen: true,// 
readmodel: true,// 
htmlcode: true,// html
help: true
}
}
},
components:{
Group,XTextarea,
XButton,
mavonEditor,
Selector
},
methods:{
addtopic(){
var app = this;
this.$http.post('/topics',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:this.$store.state.accesstoken
}).then(function(response){
if(response.data.success&&response.data){


app.$router.replace('/topiclist/dev')
}
})
},

uploadImage(pos,file){
var app = this


console.log(pos);
console.log(file);

var params = new FormData();
params.append('smfile',file);


axios.post('https://sm.ms/api/upload',params,{

headers: { 'Content-Type': 'multipart/form-data' },
}).then(function(response){

app.$refs.md.$img2Url(pos, response.data.data.url);

console.log(response.data)
})

}
}
}



