
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'user',
props:['loginname'],
components:{
Card,
Group,Cell
},
data:function(){
return {
user:'',
collect_topic:''
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/user/'+to.params.loginname).then(function(response){
if(response.data.success){
vm.user = response.data.data;
console.log(response.data.data)
}
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
if(response.data.success){
console.log(response.data)
vm.collect_topic = response.data.data
}
});

})
},
beforeRouteUpdate:function(to,from,next){
var app = this;
this.$http.get('/user/'+to.params.loginname).then(function(response){
console.log(response.data);
if(response.data.success){
app.user = response.data.data;
console.log(response.data.data)
}
});

this.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
if(response.data.success){
app.collect_topic = response.data.data
}
});
next();
}
}



