

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState} from 'vuex'
export default {
name:'topiclist',
props:['name'],
components:{
Group,
Cell,
Scroller,
Tab,TabItem
},

data:function(){
return {
lists:[],
page:1,
limit:15,

pullupConfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:''
},
pulldownConfig:{
content:'',
downContent:'',
upContent:'',
loadingContent:''
}
}
},
computed:{
topics:function(){
return this.$store.state.topics;
},
...mapState(['topics'])
},
methods:{

getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab)
},

loadDownData(){
var app = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){

app.lists = response.data.data;
app.page = 1;

app.$refs.pullup.donePulldown();
}
})
},

loadMoreData(){
var app = this;
this.page = this.page+1;
this.$http.get('/topics',{
params:{

tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){

if(response.data.success){

for(var i=0;i<response.data.data.length;i++){
app.lists.push(response.data.data[i]);
}


app.$refs.pullup.donePullup()
}
})
},

routeto(index){






this.$router.push('/topiclist/'+this.topics[index].name)
}
},


beforeRouteEnter:function(to,from,next){




next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){



if(response.data.success){
console.log(response.data.data)
vm.lists = response.data.data;
console.log(vm.lists)
}
})
})
},

beforeRouteUpdate:function(to,from,next){

console.log(this.lists)
var app = this;
this.$http.get('/topics',{
params:{

tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){

if(response.data.success){


app.lists = response.data.data
}
})

next();
}
}



