
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState,mapGetters,mapMutations} from 'vuex';
import VmBackTop from 'vue-multiple-back-top'
export default {
name: 'app',
components:{
Tab,
TabItem,
ButtonTab,ButtonTabItem,
XHeader,
VmBackTop,
Badge
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['islogined','unloginmenus','loginmenus']),

isShowMoreMenus:{

get:function(){
return this.$store.state.isShowMoreMenus;
},

set:function(nval){

this.$store.commit('mutationIsShowMoreMenus',nval);
}
}
},
data:function(){
return {
text:''
}
},

created:function(){

var loginState = window.localStorage.getItem('loginStatus');
console.log(loginState)

if(loginState){
console.log(1);

this.mutationLogin();
this.mutationLoginname(window.localStorage.getItem('loginname'));
this.mutationAccessToken(window.localStorage.getItem('accessToken'))
this.mutationId(window.localStorage.getItem('author_id'))

var app = this;
this.getUnreadMsgCount();

window.setInterval(function(){
app.getUnreadMsgCount();
},1*60*1000)
}




},
methods:{

getUnreadMsgCount(){
var app = this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(response){
if(response.data.success){
app.text = response.data.data
}
})
},

routetohome(){
this.$router.replace('/')
},

showMoreMenus(){

this.isShowMoreMenus = true

},
handleClickMenu(menukey,menuitem){


this.$router.push('/'+menukey)
},
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationId'])
}
}



